import React from 'react'
import { useDispatch } from 'react-redux'
import { Button, Modal } from 'react-bootstrap'

import { updateEmployee } from '~/redux/employeesSlice'


const LockEmployee = ({ visible, setVisible, employeeInfo }) => {
    const dispatch = useDispatch()

    // Hm x l kha ti khon
    const handleLock = (employeeInfo) => {
        dispatch(updateEmployee({
            ...employeeInfo,
            active: !employeeInfo.active
        }))
        setVisible(false)
    }

    return (
        <Modal
            scrollable
            show={visible}
            onHide={() => setVisible(false)}
        >
            <Modal.Header closeButton>
                <Modal.Title>
                    {
                        employeeInfo.active ? <>Kha ti khon - {employeeInfo.name}</> : <>M kha ti khon - {employeeInfo.name}</>
                    }
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                {
                    employeeInfo.active ? <>
                        Bn c chc chn mun kha ti khon {" "}
                        <span className="fw-bolder">
                            {employeeInfo.name}
                        </span>?
                    </> : <>
                        Bn c chc chn mun m kha kha ti khon {" "}
                        <span className="fw-bolder">
                            {employeeInfo.name}
                        </span>?
                    </>
                }

            </Modal.Body>
            <Modal.Footer>
                <Button
                    variant="secondary"
                    onClick={() => setVisible(false)}
                >
                    Hy
                </Button>
                <Button
                    variant="primary"
                    onClick={() => handleLock(employeeInfo)}
                >
                    Xc nhn
                </Button>
            </Modal.Footer>
        </Modal>
    )
}

export default LockEmployee