import React from 'react'
import { CSVLink } from 'react-csv'

import downloadIcon from '~/assets/icons/download.svg'

const ExportDataToCSV = ({ data }) => {
    let dataReport = data.map(item => {
        return {
            ...item,
            gender: item.gender === 0 ? "N" : "Nam",
            username: item.user.enableLogin === true ? item.user.username : "Khng c ng nhp",
            active: item.active === true ? "ang hot ng" : "Khng hot ng",
            departments: item.departments?.length > 0 ? item.departments.map(department => {
                return "" + department.name + ":" + department.position.name
            }).join("\n") : "Cha  trong bt phng ban no",
            teams: item.teams?.length > 0 ? item.teams.map(team => {
                return "" + team.name + ":" + team.position.name
            }).join("\n") : "Cha tham gia i nhm"
        }
    })
    const headers = [
        { label: "Id", key: "id" },
        { label: "M", key: "code" },
        { label: "H v tn", key: "name" },
        { label: "Ngy sinh", key: "dateOfBirth" },
        { label: "Email", key: "email" },
        { label: "S in thoi", key: "phoneNumber" },
        { label: "Link nh i din", key: "avatar" },
        { label: "Gii tnh", key: "gender" },
        { label: "Tn ngi dng", key: "username" },
        { label: "Trng thi hot ng", key: "active" },
        { label: "Ngy to", key: "createDate" },
        { label: "To bi", key: "createBy" },
        { label: "Ngy chnh sa", key: "nodifyDate" },
        { label: "Chnh sa bi", key: "modifyBy" },
        { label: "Danh sch phng ban", key: "departments" },
        { label: "Danh sch i nhm", key: "teams" }
    ]
    const csvReport = {
        data: dataReport,
        headers: headers,
        filename: "Employee" + Date() + ".csv"

    }
    return (
        <CSVLink
            {...csvReport}
            className="btn"
        >
            <img src={downloadIcon} alt="Download icon" />
        </CSVLink>
    )
}

export default ExportDataToCSV