import React, { useEffect, useState } from "react"
import { Button, Form, Modal } from "react-bootstrap"
import { useDispatch } from "react-redux"

import { addTeam, updateTeam } from "~/redux/teamsSlice"
import Positions from "./Positions"

const FormSubmitTeam = ({ visible, setVisible, team = null }) => {
    const dispatch = useDispatch()

    /* Qun l cc state */
    const [teamInfo, setTeamInfo] = useState({
        // State lu thng tin ca i nhm khi ngi dng nhp d liu
        code: "",
        name: "",
        description: "",
        positions: []
    })
    //

    useEffect(() => {
        if (team?.id) {
            setTeamInfo(team)
        }
    }, [team])

    /* Cc hm thay i gi tr ca state teamInfo mi khi ngi dng nhp/chn d liu mi */
    const handleInputChange = (e) => {
        setTeamInfo({
            ...teamInfo,
            [e.target.name]: e.target.value
        })
    }
    //

    /* X l Submit Form */
    const [validated, setValidated] = useState(false)
    const handleSubmit = (e) => {
        const form = e.currentTarget
        if (form.checkValidity() === false) {
            e.preventDefault()
            e.stopPropagation()
        }
        setValidated(true)
        if (form.checkValidity() === true) {
            e.preventDefault()
            e.stopPropagation()
            if (teamInfo.id) {
                dispatch(updateTeam(teamInfo))
            }
            else {
                dispatch(addTeam(teamInfo))
            }
        }
    }
    //

    return (
        <Modal
            className="modal-fullheight"
            size="md"
            scrollable
            show={visible}
            onHide={() => setVisible(false)}
        >
            <Modal.Header closeButton>
                <Modal.Title>
                    {team?.id ? "CHNH SA I NHM" : "THM I NHM MI"}
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form
                    noValidate
                    validated={validated}
                    onSubmit={handleSubmit}
                >
                    <div className="mb-3">
                        <Form.Label>M i nhm:</Form.Label>
                        <Form.Control
                            type="text"
                            name="code"
                            placeholder="Nhp m i nhm..."
                            value={teamInfo.code}
                            onChange={handleInputChange}
                            required
                        />
                        <Form.Control.Feedback type="invalid">
                            Vui lng nhp m i nhm.
                        </Form.Control.Feedback>
                    </div>
                    <hr />
                    <div className="mb-3">
                        <Form.Label>Tn i nhm:</Form.Label>
                        <Form.Control
                            type="text"
                            name="name"
                            placeholder="Nhp tn i nhm..."
                            value={teamInfo.name}
                            onChange={handleInputChange}
                            required
                        />
                        <Form.Control.Feedback type="invalid">
                            Vui lng nhp tn i nhm.
                        </Form.Control.Feedback>
                    </div>
                    <hr />
                    <div className="mb-3">
                        <Form.Label>M t:</Form.Label>
                        <Form.Control
                            as="textarea"
                            rows={10}
                            name="description"
                            placeholder="Nhp m t i nhm..."
                            value={teamInfo.description}
                            onChange={handleInputChange}
                        />
                    </div>
                    <hr />
                    <Positions teamInfo={teamInfo} setTeamInfo={setTeamInfo} />
                    <Button
                        className="d-table m-auto"
                        size="lg"
                        type="submit"
                    >
                        {(team?.id) ? "Cp nht thng tin" : "Xc nhn to mi"}
                    </Button>
                </Form>
            </Modal.Body>
        </Modal>
    )
}

export default FormSubmitTeam
