/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { Button, Form, Modal } from "react-bootstrap"

import { addDepartment, updateDepartment } from "~/redux/departmentsSlice"
import { departmentsSelector } from "~/redux/selectors"
import FormSelectDepartment from "../../EmployeesFeatures/SubmitEmployee/FormSelectDepartment"
import Positions from "./Positions"

const FormSubmitDepartment = ({ visible, setVisible, department = null }) => {
    const departments = useSelector(departmentsSelector).departments
    const dispatch = useDispatch()

    /* Qun l cc state */
    const [departmentInfo, setDepartmentInfo] = useState({
        // State lu thng tin ca phng ban khi ngi dng nhp d liu
        code: "",
        name: "",
        description: "",
        fatherDepartmentId: null,
        positions: []
    })
    //

    useEffect(() => {
        if (department?.id) {
            setDepartmentInfo(department)
        }
    }, [department])

    /* Cc hm thay i gi tr ca state departmentInfo mi khi ngi dng nhp/chn d liu mi */
    const handleInputChange = (e) => {
        setDepartmentInfo({
            ...departmentInfo,
            [e.target.name]: e.target.value
        })
    }
    const handleDepartmentChange = (index, department) => {
        setDepartmentInfo({
            ...departmentInfo,
            level: department.level + 1,
            fatherDepartmentId: department.id
        })
    }
    //

    /* X l Submit Form */
    const [validated, setValidated] = useState(false)
    const handleSubmit = (e) => {
        const form = e.currentTarget
        if (form.checkValidity() === false) {
            e.preventDefault()
            e.stopPropagation()
        }
        setValidated(true)
        if (form.checkValidity() === true) {
            e.preventDefault()
            e.stopPropagation()
            if (departmentInfo.id) {
                dispatch(updateDepartment({
                    ...departmentInfo,
                    fatherDepartmentId: departmentInfo.fatherDepartmentId === -1 ? null : departmentInfo.fatherDepartmentId
                }))
            }
            else {
                dispatch(addDepartment({
                    ...departmentInfo,
                    fatherDepartmentId: departmentInfo.fatherDepartmentId === -1 ? null : departmentInfo.fatherDepartmentId
                }))
            }
            setVisible(false)
        }
    }
    //

    // Tm phng ban cha da theo id
    const fatherDepartment = departments.find(department => department.id === departmentInfo.fatherDepartmentId) || {
        id: null,
        name: "Khng c phng ban cha"
    }
    return (
        <Modal
            className="modal-fullheight"
            size="md"
            scrollable
            show={visible}
            onHide={() => setVisible(false)}
        >
            <Modal.Header closeButton>
                <Modal.Title>
                    {department?.id ? "CHNH SA PHNG BAN" : "THM PHNG BAN MI"}
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form
                    noValidate
                    validated={validated}
                    onSubmit={handleSubmit}
                >
                    <div className="mb-3">
                        <Form.Label>M phng ban:</Form.Label>
                        <Form.Control
                            type="text"
                            name="code"
                            placeholder="Nhp m phng ban..."
                            value={departmentInfo.code}
                            onChange={handleInputChange}
                            required
                        />
                        <Form.Control.Feedback type="invalid">
                            Vui lng nhp m phng ban.
                        </Form.Control.Feedback>
                    </div>
                    <hr />
                    <div className="mb-3">
                        <Form.Label>Tn phng ban:</Form.Label>
                        <Form.Control
                            type="text"
                            name="name"
                            placeholder="Nhp tn phng ban..."
                            value={departmentInfo.name}
                            onChange={handleInputChange}
                            required
                        />
                        <Form.Control.Feedback type="invalid">
                            Vui lng nhp tn phng ban.
                        </Form.Control.Feedback>
                    </div>
                    <hr />
                    <div className="mb-3">
                        <Form.Label>M t:</Form.Label>
                        <Form.Control
                            as="textarea"
                            rows={10}
                            name="description"
                            placeholder="Nhp m t phng ban..."
                            value={departmentInfo.description}
                            onChange={handleInputChange}
                        />
                    </div>
                    <hr />
                    <div className="mb-3">
                        <Form.Label>Phng ban cha:</Form.Label>
                        <FormSelectDepartment
                            index={null}
                            currentDepartment={fatherDepartment}
                            onDepartmentChange={handleDepartmentChange}
                            departments={departments}
                        />
                    </div>
                    <Positions departmentInfo={departmentInfo} setDepartmentInfo={setDepartmentInfo} />
                    <Modal.Footer>
                        <Button
                            className="d-table m-auto"
                            size="lg"
                            type="submit"
                        >
                            {(department?.id) ? "Cp nht thng tin" : "Xc nhn to mi"}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal.Body>
        </Modal>
    )
}

export default FormSubmitDepartment
