/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { Dropdown, ListGroup, Modal, Table } from 'react-bootstrap'
import { BiInfoSquare } from 'react-icons/bi'

import { departmentsSelector } from '~/redux/selectors'
import departmentsApi from '~/api/departmentsApi'

const DepartmentDetail = ({ visible, setVisible, departmentId }) => {
    const departments = useSelector(departmentsSelector).departments    // Ly danh sch phng ban t redux

    const [departmentInfo, setDepartmentInfo] = useState({})

    let parentName = ""     // Khi to tn phng ban cha mc nh l rng

    useEffect(() => {
        departmentsApi.getDepartmentDetailById(departmentId)
            .then((response) => {
                setDepartmentInfo(response.data.data)
            })
    }, [])

    // Duyt qua danh sch phng ban  tm ra tn phng ban cha
    departments.forEach(dp => {
        if (dp.id === departmentInfo.fatherDepartmentId) {
            parentName = dp.name
        }
    })

    return (
        <Modal
            className="modal-fullheight"
            size="md"
            scrollable
            show={visible}
            onHide={() => setVisible(false)}
        >
            <Modal.Header closeButton>
                <Modal.Title>
                    Chi tit phng ban
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <ListGroup variant="flush">
                    <ListGroup.Item>
                        M phng ban: {departmentInfo.code}
                    </ListGroup.Item>
                    <ListGroup.Item>
                        Tn phng ban: {departmentInfo.name}
                    </ListGroup.Item>
                    <ListGroup.Item>
                        Thuc s qun l ca phng ban: {parentName || departmentInfo.name}
                    </ListGroup.Item>
                    <ListGroup.Item>
                        M t v phng ban: {departmentInfo.description === "" ? "Cha c m t" : departmentInfo.description}
                    </ListGroup.Item>
                    <ListGroup.Item>
                        <Table
                            striped
                            hover
                            responsive
                            borderless
                        >
                            <thead>
                                <tr>
                                    <td>
                                        <span className="fw-bolder">
                                            CHC V
                                        </span>
                                    </td>
                                    <td>
                                        <span className="fw-bolder">
                                            VAI TR
                                        </span>
                                    </td>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    departmentInfo.positions?.map((position, index) => (
                                        <tr key={index}>
                                            <td>{position.name}</td>
                                            <td>{position.role?.name}</td>
                                        </tr>
                                    ))
                                }
                            </tbody>
                        </Table>
                    </ListGroup.Item>
                </ListGroup>
            </Modal.Body>
        </Modal>
    )
}

export default DepartmentDetail