import React from 'react'
import { Image } from 'react-bootstrap'

import searchIcon from '../assets/icons/search.svg'
/*
    Truyn vo 2 props:
    1. value: gi tr ca state gi gi tr ang tm kim
    2. onSearch: hm callback s set li gi tr ca state khi ngi dung nhp gi tr mi
*/
const AppSearch = ({ value, onSearch }) => {
    return (
        <div className="d-flex justify-content-center">
            <div
                className="d-inline-flex form-control py-1"
                style={{ borderRadius: "0.5rem" }}
            >
                <Image
                    src={searchIcon}
                    className="mx-auto h-100"
                    style={{
                        marginTop: "0.1rem"
                    }}
                />
                <input
                    className="w-100"
                    type="search"
                    style={{
                        border: "none",
                        outline: "none",
                        color: "#2F6BB1",
                        fontSize: "14px"
                    }}
                    placeholder="Tm kim..."
                    value={value || ""}
                    onChange={(e) => onSearch(e.target.value)}
                />
            </div>
        </div>
    )
}

export default React.memo(AppSearch)