import React, { useEffect, useState } from 'react'
import { Image, Pagination } from 'react-bootstrap'

import previousPageIcon from '../assets/icons/previous_page.svg'
import nextPageIcon from '../assets/icons/next_page.svg'

/*
    Truyn vo 2 props:
    1. pagination: mt i tng cha 3 thuc tnh:
    + page: trang hin ti
    + limit: s dng trn mi trang
    + totalItem: tng s Item ca danh sch
    2. onPageChange: mt hm callback nhn vo mt i s l gi tr trang mi khi click chut vo button
*/

const AppPagination = ({ pagination, onPageChange }) => {
    // Ly cc i tng t props pagination
    let { page, limit, totalItem } = pagination
    page = Number.parseInt(page)

    // Tnh ton tng s trang
    const totalPage = Math.ceil(totalItem / limit)

    // State lu chiu rng hin ti ca trnh duyt
    const [width, setWidth] = useState(window.innerWidth)

    // Hm cp nht state width mi khi trnh duyt thay i  rng
    const updateWidth = () => {
        setWidth(window.innerWidth)
    }

    // Effect c tc dng thm hm cp nht state width cho event resize
    useEffect(() => {
        window.addEventListener('resize', updateWidth)
        return () => {
            window.removeEventListener('resize', updateWidth)
        }
    }, [width])

    // Hm ny s tr v mt mng lu nhng s (hoc du ...) cn hin th ra UI ty thuc vo chiu rng ca mn hnh ( responsive)
    const fetchListPage = () => {
        // Khi to listPage l mng rng
        const listPage = []

        // Thc hin push nhng g cn hin th vo listPage ty thuc vo gi tr ca width
        if (width < 576) {
            if (totalPage >= 8) {
                if (page < 2) {
                    for (let i = 1; i <= 2; ++i) {
                        listPage.push(i)
                    }
                    listPage.push("...")
                    listPage.push(totalPage)
                }
                else if (page > totalPage - 1) {
                    listPage.push(1)
                    listPage.push("...")
                    for (let i = totalPage - 1; i <= totalPage; ++i) {
                        listPage.push(i)
                    }
                }
                else {
                    listPage.push(page - 1)
                    listPage.push(page)
                    listPage.push(page + 1)
                }
            }
            else if (totalPage >= 5) {
                if (page <= 2) {
                    for (let i = 1; i <= 3; ++i) {
                        listPage.push(i)
                    }
                    listPage.push("...")
                    listPage.push(totalPage)
                }
                else if (page > totalPage - 2) {
                    listPage.push(1)
                    listPage.push("...")
                    for (let i = totalPage - 2; i <= totalPage; ++i) {
                        listPage.push(i)
                    }
                }
                else {
                    listPage.push(1)
                    listPage.push("...")
                    for (let i = page - 1; i <= page + 1; ++i) {
                        listPage.push(i)
                    }
                    listPage.push("...")
                    listPage.push(totalPage)
                }
            }
            else {
                for (let i = 1; i <= totalPage; ++i) {
                    listPage.push(i)
                }
            }
        }
        else if (width < 992) {
            if (totalPage >= 8) {
                if (page < 3) {
                    for (let i = 1; i <= 3; ++i) {
                        listPage.push(i)
                    }
                    listPage.push("...")
                    listPage.push(totalPage)
                }
                else if (page > totalPage - 2) {
                    listPage.push(1)
                    listPage.push("...")
                    for (let i = totalPage - 2; i <= totalPage; ++i) {
                        listPage.push(i)
                    }
                }
                else {
                    listPage.push(1)
                    listPage.push("...")
                    for (let i = page - 1; i <= page + 1; ++i) {
                        listPage.push(i)
                    }
                    listPage.push("...")
                    listPage.push(totalPage)
                }
            }
            else if (totalPage >= 5) {
                if (page <= 2) {
                    for (let i = 1; i <= 3; ++i) {
                        listPage.push(i)
                    }
                    listPage.push("...")
                    listPage.push(totalPage)
                }
                else if (page > totalPage - 2) {
                    listPage.push(1)
                    listPage.push("...")
                    for (let i = totalPage - 2; i <= totalPage; ++i) {
                        listPage.push(i)
                    }
                }
                else {
                    listPage.push(1)
                    listPage.push("...")
                    for (let i = page - 1; i <= page + 1; ++i) {
                        listPage.push(i)
                    }
                    listPage.push("...")
                    listPage.push(totalPage)
                }
            }
            else {
                for (let i = 1; i <= totalPage; ++i) {
                    listPage.push(i)
                }
            }
        }
        else {
            if (totalPage >= 8) {
                if (page < 4) {
                    for (let i = 1; i <= 4; ++i) {
                        listPage.push(i)
                    }
                    listPage.push("...")
                    listPage.push(totalPage)
                }
                else if (page > totalPage - 3) {
                    listPage.push(1)
                    listPage.push("...")
                    for (let i = totalPage - 3; i <= totalPage; ++i) {
                        listPage.push(i)
                    }
                }
                else {
                    listPage.push(1)
                    listPage.push("...")
                    for (let i = page - 2; i <= page + 2; ++i) {
                        listPage.push(i)
                    }
                    listPage.push("...")
                    listPage.push(totalPage)
                }
            }
            else if (totalPage >= 5) {
                if (page <= 2) {
                    for (let i = 1; i <= 3; ++i) {
                        listPage.push(i)
                    }
                    listPage.push("...")
                    listPage.push(totalPage)
                }
                else if (page > totalPage - 2) {
                    listPage.push(1)
                    listPage.push("...")
                    for (let i = totalPage - 2; i <= totalPage; ++i) {
                        listPage.push(i)
                    }
                }
                else {
                    listPage.push(1)
                    listPage.push("...")
                    for (let i = page - 1; i <= page + 1; ++i) {
                        listPage.push(i)
                    }
                    listPage.push("...")
                    listPage.push(totalPage)
                }
            }
            else {
                for (let i = 1; i <= totalPage; ++i) {
                    listPage.push(i)
                }
            }
        }
        return listPage
    }

    return (
        <div className="d-table m-auto">
            <Pagination
                className="col"
                size={
                    width < 576 ? "sm" : (width < 992 ? "" : "lg")
                }
            >
                <Pagination.Item
                    disabled={page <= 1}
                    onClick={() => onPageChange(page - 1)}
                >
                    <Image src={previousPageIcon} />
                </Pagination.Item>
                {
                    fetchListPage().map((pageItem, index) => {
                        if (pageItem === page) {
                            return (
                                <Pagination.Item
                                    key={index}
                                    active
                                >
                                    {pageItem}
                                </Pagination.Item>
                            )
                        }
                        else if (pageItem === "...") {
                            return (
                                <Pagination.Item
                                    disabled
                                    key={index}
                                >
                                    {pageItem}
                                </Pagination.Item>
                            )
                        }
                        else {
                            return (
                                <Pagination.Item
                                    key={index}
                                    onClick={() => onPageChange(pageItem)}
                                >
                                    {pageItem}
                                </Pagination.Item>
                            )
                        }
                    })
                }
                <Pagination.Item
                    disabled={page >= totalPage}
                    onClick={() => onPageChange(page + 1)}
                >
                    <Image src={nextPageIcon} />
                </Pagination.Item>
            </Pagination>
        </div>
    )
}

export default React.memo(AppPagination)