import React, { useRef, useState } from 'react'

import { ListGroup } from 'react-bootstrap'
import useOnClickOutside from '../../../../customHooks/useOnClickOutside'

const FormSelectPosition = ({ index, current, positions, onChange }) => {
    const [visible, setVisible] = useState(false)       // State qun l hin th danh sch phng ban

    const ref = useRef()        // Ref form select position

    useOnClickOutside(ref, () => setVisible(false))     // Hm x l ng form select position khi click ra ngoi

    let selectPositionElement = null
    if (positions === null) {
        selectPositionElement = <ListGroup.Item>Cha chn phng ban</ListGroup.Item>
    }
    else if (positions?.length === 0) {
        selectPositionElement = <ListGroup.Item>Phng ban cha c chc v</ListGroup.Item>
    }
    else {
        selectPositionElement = positions?.map((position) => (
            <ListGroup.Item
                action
                key={position.id}
                onClick={() => onChange(index, position)}
                active={current?.name === position.name}
            >
                {position.name}
            </ListGroup.Item>
        ))
    }

    return (
        <div
            ref={ref}
            onClick={() => setVisible(!visible)}
            className="form-select"
        >
            {current?.name || "Chn chc v"}
            <div className="select">
                {(visible) ? selectPositionElement : null}
            </div>
        </div>
    )
}

export default FormSelectPosition