/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { Button, Form, Modal, Tab, Tabs } from "react-bootstrap"
import { BiPlusMedical, BiTrash } from 'react-icons/bi'
import PropTypes from 'prop-types'

import { addEmployee, updateEmployee } from "~/redux/employeesSlice"
import { departmentsSelector, teamsSelector } from "~/redux/selectors"
import FormSelectDepartment from "./FormSelectDepartment"
import FormSelectPosition from "./FormSelectPosition"
import FormSelectTeam from "./FormSelectTeam"
import { getDepartmentList } from "~/redux/departmentsSlice"
import { getTeamList } from "~/redux/teamsSlice"

const propTypes = {
    visible: PropTypes.bool.isRequired,
    setVisible: PropTypes.func.isRequired,
    employee: PropTypes.object
}

const FormSubmitEmployee = ({ visible, setVisible, employee = null }) => {
    const departments = useSelector(departmentsSelector).departments
    const teams = useSelector(teamsSelector).teams
    const dispatch = useDispatch()

    /* Qun l cc state */
    const [employeeInfo, setEmployeeInfo] = useState({
        code: "",
        name: "",
        avatar: "",
        dateOfBirth: "",
        gender: "",
        email: "",
        phoneNumber: "",
        teams: [],
        departments: [],
        user: {
            username: "",
            enableLogin: false
        }
    })
    const [tab, setTab] = useState('departments')       // State chuyn t iu chnh phng ban <=> iu chnh i nhm
    //

    useEffect(() => {
        dispatch(getDepartmentList())
        dispatch(getTeamList())
    }, [])

    useEffect(() => {
        if (employee?.id) {
            let data = {
                ...employee,
                user: (employee.user.enableLogin !== false) ? employee.user : { username: "" },
                departments: employee.departments.map((department, index) => {
                    return {
                        ...employee.departments[index],
                        positions: departments.find(dp => dp.id === department.id)?.positions
                    }
                }),
                teams: employee.teams.map((team, index) => {
                    return {
                        ...employee.teams[index],
                        positions: teams.find(tm => tm.id === team.id)?.positions
                    }
                })
            }
            setEmployeeInfo(data)
        }
    }, [])

    /* Cc hm thay i gi tr ca state employeeInfo mi khi ngi dng nhp/chn d liu mi */
    const handleInputChange = (e) => {
        if (e.target.type === "checkbox") {
            if (e.target.checked) {
                setEmployeeInfo({
                    ...employeeInfo,
                    user: {
                        username: null,
                        enableLogin: false
                    }
                })
            }
            else {
                setEmployeeInfo({
                    ...employeeInfo,
                    user: {
                        username: employeeInfo.email,
                        enableLogin: true
                    }
                })
            }
        }
        else {
            setEmployeeInfo({
                ...employeeInfo,
                [e.target.name]: e.target.value
            })
        }
    }
    const handleUserChange = (e) => {
        setEmployeeInfo({
            ...employeeInfo,
            user: {
                ...employeeInfo.user,
                [e.target.name]: e.target.value
            }
        })
    }
    const handleToggleLogin = (e) => {
        if (e.target.checked) {
            setEmployeeInfo({
                ...employeeInfo,
                user: {
                    username: employeeInfo.user.username || employeeInfo.email,
                    enableLogin: true
                }
            })
        }
        else {
            setEmployeeInfo({
                ...employeeInfo,
                user: {
                    username: "",
                    enableLogin: false
                }
            })
        }
    }
    //

    /* X l khi click vo button Thm phng ban */
    const handleShowFormSelectDepartment = () => {
        if (employeeInfo.departments?.length === 0) {
            setEmployeeInfo({
                ...employeeInfo,
                departments: [{}]
            })
        }
        else {
            setEmployeeInfo({
                ...employeeInfo,
                departments: [
                    ...employeeInfo.departments,
                    {}
                ]
            })
        }
    }

    const handleDepartmentChange = (index, newDepartment) => {
        let newDepartments = employeeInfo.departments.map((department, key) => {
            if (key === index) {
                return {
                    ...newDepartment,
                    position: {}
                }
            }
            return department
        })
        setEmployeeInfo({
            ...employeeInfo,
            departments: newDepartments
        })
    }

    const handlePositionOfDepartmentChange = (index, position) => {
        let newDepartments = employeeInfo.departments.map((department, key) => {
            if (key === index) {
                return {
                    ...department,
                    position
                }
            }
            return department
        })
        setEmployeeInfo({
            ...employeeInfo,
            departments: newDepartments
        })
    }

    const handleDeleteFormSelectDepartment = (index) => {
        const newDepartments = employeeInfo.departments.filter((e, idx) => index !== idx)
        setEmployeeInfo({
            ...employeeInfo,
            departments: newDepartments
        })
    }
    //

    /* X l khi click vo button Thm i nhm */
    const handleShowFormSelectTeam = () => {
        if (employeeInfo.teams?.length === 0) {
            setEmployeeInfo({
                ...employeeInfo,
                teams: [{}]
            })
        }
        else {
            setEmployeeInfo({
                ...employeeInfo,
                teams: [
                    ...employeeInfo.teams,
                    {}
                ]
            })
        }
    }

    const handleTeamChange = (index, newTeam) => {
        let newTeams = employeeInfo.teams.map((team, key) => {
            if (key === index) {
                return {
                    ...newTeam,
                    position: {}
                }
            }
            return team
        })
        setEmployeeInfo({
            ...employeeInfo,
            teams: newTeams
        })
    }

    const handlePositionOfTeamChange = (index, position) => {
        let newTeams = employeeInfo.teams.map((team, key) => {
            if (key === index) {
                return {
                    ...team,
                    position
                }
            }
            return team
        })
        setEmployeeInfo({
            ...employeeInfo,
            teams: newTeams
        })
    }

    const handleDeleteFormSelectTeam = (index) => {
        const newTeams = employeeInfo.teams.filter((e, idx) => index !== idx)
        setEmployeeInfo({
            ...employeeInfo,
            teams: newTeams
        })
    }
    //

    /* X l Submit Form */
    const [validated, setValidated] = useState(false)
    const handleSubmit = (e) => {
        const form = e.currentTarget
        if (form.checkValidity() === false) {
            e.preventDefault()
            e.stopPropagation()
        }
        setValidated(true)
        if (form.checkValidity() === true) {
            e.preventDefault()
            e.stopPropagation()
            if (employeeInfo.id) {
                let data = {
                    ...employeeInfo,
                    modifyBy: 1
                }
                data.departments?.forEach((department, index, array) => {
                    delete array[index].positions
                })
                if (data.hasOwnProperty("team") === false) {
                    data.teams = []
                }
                data.teams?.forEach((team, index, array) => {
                    delete array[index].positions
                })
                setVisible(false)
                dispatch(updateEmployee(data))
            }
            else {
                let data = {
                    ...employeeInfo,
                    createBy: 1
                }
                data.departments?.forEach((department, index, array) => {
                    delete array[index].positions
                })
                data.teams?.forEach((team, index, array) => {
                    delete array[index].positions
                })
                setVisible(false)
                dispatch(addEmployee(data))
            }
        }
    }
    //

    return (
        <Modal
            className="modal-fullheight"
            size="lg"
            scrollable
            show={visible}
            onHide={() => setVisible(false)}
        >
            <Modal.Header closeButton>
                <Modal.Title className="text-white">
                    {employee?.id ? "Chnh sa nhn vin" : "Thm nhn vin"}
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form
                    noValidate
                    validated={validated}
                    onSubmit={handleSubmit}
                >
                    <div className="modal-body-content">
                        <div className="mb-3">
                            <Form.Label>M nhn vin:</Form.Label>
                            <Form.Control
                                type="text"
                                name="code"
                                placeholder="Nhp m nhn vin..."
                                value={employeeInfo.code}
                                onChange={handleInputChange}
                                isInvalid={employeeInfo.code?.length > 10}
                                required
                            />
                            <Form.Control.Feedback type="invalid">
                                M nhn vin khng hp l.
                            </Form.Control.Feedback>
                            M nhn vin khng c vt qu 10 k t.
                        </div>
                        <hr />
                        <div className="mb-3">
                            <Form.Label>H v tn:</Form.Label>
                            <Form.Control
                                type="text"
                                name="name"
                                placeholder="Nhp h v tn nhn vin..."
                                value={employeeInfo.name}
                                onChange={handleInputChange}
                                required
                            />
                            <Form.Control.Feedback type="invalid">
                                Vui lng nhp h v tn nhn vin.
                            </Form.Control.Feedback>
                        </div>
                        <hr />
                        <div className="mb-3">
                            <Form.Label>Ngy sinh:</Form.Label>
                            <Form.Control
                                type="date"
                                name="dateOfBirth"
                                placeholder="Nhp ngy sinh..."
                                value={employeeInfo.dateOfBirth || ""}
                                onChange={handleInputChange}
                                required
                            />
                            <Form.Control.Feedback type="invalid">
                                Vui lng nhp ngy sinh.
                            </Form.Control.Feedback>
                        </div>
                        <hr />
                        <div className="mb-3">
                            <Form.Label>Gii tnh:</Form.Label>
                            <Form.Select
                                name="gender"
                                value={employeeInfo.gender}
                                onChange={handleInputChange}
                                required
                            >
                                <option value="0">N</option>
                                <option value="1">Nam</option>
                            </Form.Select>
                            <Form.Control.Feedback type="invalid">
                                Vui lng nhp ngy sinh.
                            </Form.Control.Feedback>
                        </div>
                        <hr />
                        <div className="mb-3">
                            <Form.Label>Email:</Form.Label>
                            <Form.Control
                                type="email"
                                name="email"
                                placeholder="Nhp email nhn vin..."
                                value={employeeInfo.email}
                                onChange={handleInputChange}
                                required
                            />
                            <Form.Control.Feedback type="invalid">
                                Vui lng nhp email.
                            </Form.Control.Feedback>
                        </div>
                        <hr />
                        <div className="mb-3">
                            <Form.Label>S in thoi:</Form.Label>
                            <Form.Control
                                type="number"
                                name="phoneNumber"
                                placeholder="Nhp s in thoi ca nhn vin..."
                                value={employeeInfo.phoneNumber}
                                onChange={handleInputChange}
                                required
                            />
                            <Form.Control.Feedback type="invalid">
                                Vui lng nhp s in thoi.
                            </Form.Control.Feedback>
                        </div>
                        <hr />
                        <div className="card mb-3">
                            <Tabs
                                activeKey={tab}
                                onSelect={(k) => setTab(k)}
                            >
                                <Tab eventKey="departments" title="Phng ban">
                                    <div className="card-body">
                                        {
                                            employeeInfo.departments.map((department, index) => (
                                                <div key={index} className="list-group-item bg-light mb-3">
                                                    <div className="d-flex flex-lg-row flex-column">
                                                        <div className="mb-3 mb-lg-0 col">
                                                            <Form.Label>Phng ban s {index + 1}:</Form.Label>
                                                            <FormSelectDepartment
                                                                index={index}
                                                                currentDepartment={department}
                                                                onDepartmentChange={handleDepartmentChange}
                                                                departments={departments}
                                                            />
                                                        </div>
                                                        <div className="mb-3 ms-lg-3 col">
                                                            <Form.Label>Chc v ca phng ban s {index + 1}:</Form.Label>
                                                            <FormSelectPosition
                                                                index={index}
                                                                current={employeeInfo.departments[index]?.position}
                                                                positions={employeeInfo.departments[index]?.positions}
                                                                onChange={handlePositionOfDepartmentChange}
                                                            />
                                                        </div>
                                                    </div>
                                                    <Button
                                                        variant="outline-danger"
                                                        className="d-block m-auto"
                                                        onClick={() => handleDeleteFormSelectDepartment(index)}
                                                    >
                                                        <BiTrash /> Xa
                                                    </Button>
                                                </div>
                                            ))
                                        }
                                    </div>
                                    <div className="mb-3">
                                        <Button
                                            variant="outline-primary"
                                            className="d-block m-auto"
                                            onClick={handleShowFormSelectDepartment}
                                        >
                                            <BiPlusMedical />{" "} Thm phng ban {" "}<BiPlusMedical />
                                        </Button>
                                    </div>
                                </Tab>
                                <Tab eventKey="teams" title="i nhm">
                                    <div className="card-body">
                                        {
                                            employeeInfo.teams?.map((team, index) => (
                                                <div key={index} className="list-group-item bg-light mb-3">
                                                    <div className="d-flex flex-lg-row flex-column">
                                                        <div className="mb-3 mb-lg-0 col">
                                                            <Form.Label>i nhm s {index + 1}:</Form.Label>
                                                            <FormSelectTeam
                                                                index={index}
                                                                currentTeam={team}
                                                                onTeamChange={handleTeamChange}
                                                                teams={teams}
                                                            />
                                                        </div>
                                                        <div className="mb-3 ms-lg-3 col">
                                                            <Form.Label>Chc v ca i nhm s {index + 1}:</Form.Label>
                                                            <FormSelectPosition
                                                                index={index}
                                                                current={employeeInfo.teams[index]?.position}
                                                                positions={employeeInfo.teams[index]?.positions}
                                                                onChange={handlePositionOfTeamChange}
                                                            />
                                                        </div>
                                                    </div>
                                                    <Button
                                                        variant="outline-danger"
                                                        className="d-block m-auto"
                                                        onClick={() => handleDeleteFormSelectTeam(index)}
                                                    >
                                                        <BiTrash /> Xa
                                                    </Button>
                                                </div>
                                            ))
                                        }
                                    </div>
                                    <div className="mb-3">
                                        <Button
                                            variant="outline-primary"
                                            className="d-block m-auto"
                                            onClick={handleShowFormSelectTeam}
                                        >
                                            <BiPlusMedical />{" "} Thm i nhm {" "}<BiPlusMedical />
                                        </Button>
                                    </div>
                                </Tab>
                            </Tabs>
                        </div>
                        <hr />
                        <div className="card mb-3">
                            <div className="card-header">
                                <Form.Check
                                    type="switch"
                                    label="Cho php ng nhp"
                                    checked={employeeInfo.user.enableLogin}
                                    onChange={handleToggleLogin}
                                />
                            </div>
                            <div className="card-body">
                                {(employeeInfo.user.enableLogin) ? (
                                    <>
                                        <div className="mb-3">
                                            <Form.Label htmlFor="username" className="mt-3">Tn ng nhp:</Form.Label>
                                            <Form.Control
                                                type="text"
                                                name="username"
                                                placeholder="Nhp tn ng nhp..."
                                                value={employeeInfo.user?.username || employeeInfo.email}
                                                onChange={handleUserChange}
                                                required
                                            />
                                            <Form.Control.Feedback type="invalid">
                                                Vui lng nhp tn ng nhp.
                                            </Form.Control.Feedback>
                                        </div>
                                        {((employee?.id && employee?.user.username === "" && employee?.user.password === "cmdcmdcmd") || !employee) ? (
                                            <>
                                                <hr />
                                                <div className="mb-3">
                                                    <Form.Label htmlFor="password">Mt khu:</Form.Label>
                                                    <Form.Control
                                                        type="text"
                                                        name="password"
                                                        placeholder="Nhp mt khu..."
                                                        value={"cmacmacma"}
                                                        readOnly
                                                    />
                                                    <Form.Control.Feedback type="invalid">
                                                        Vui lng nhp mt khu.
                                                    </Form.Control.Feedback>
                                                </div>
                                            </>
                                        ) : null}
                                    </>
                                ) : null}
                            </div>
                        </div>
                    </div>
                    <Button
                        size="lg"
                        type="submit"
                        className="d-table m-auto"
                    >
                        {(employee?.id) ? "Cp nht thng tin" : "Xc nhn to mi"}
                    </Button>
                </Form>
            </Modal.Body>
        </Modal>
    )
}

FormSubmitEmployee.propTypes = propTypes

export default FormSubmitEmployee