import React, { useState } from 'react'
import { Button, Form, Offcanvas } from 'react-bootstrap'
import { Formik } from 'formik'
import * as Yup from 'yup'

const FiltersAdvanced = ({ visible, setVisible, filters, setFilters }) => {

    /* X l Form vi Formik */
    let initialValues = filters

    const validationSchema = Yup.object({
        name: Yup.string(),
        dob: Yup.date(),
        email: Yup.string(),
        phone: Yup.string(),
        dep: Yup.string(),
        pos: Yup.string()
    })

    const handleSubmit = async (values, actions) => {
        actions.setSubmitting(true)
        setFilters({
            ...filters,
            ...values
        })
        actions.setSubmitting(false)
    }
    //

    return (
        <Offcanvas
            show={visible}
            onHide={() => setVisible(false)}
            scroll={true}
            backdrop
            placement="end"
            responsive="lg"
        >
            <Offcanvas.Header className="bg-gradient text-white" closeButton>
                <Offcanvas.Title>
                    <div className="fw-bolder">
                        Lc nng cao
                    </div>
                </Offcanvas.Title>
            </Offcanvas.Header>
            <Offcanvas.Body>
                <Formik
                    initialValues={initialValues}
                    validationSchema={validationSchema}
                    onSubmit={handleSubmit}
                >
                    {
                        ({ values, handleChange, handleBlur, handleSubmit }) => (
                            <Form onSubmit={handleSubmit}>
                                <div className="mb-4">
                                    <Form.Label>H v tn:</Form.Label>
                                    <Form.Control
                                        type="text"
                                        name="name"
                                        placeholder="Nhp h v tn..."
                                        value={values.name}
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                    />
                                </div>
                                <div className="mb-4">
                                    <Form.Label>Ngy sinh:</Form.Label>
                                    <Form.Control
                                        type="date"
                                        name="dob"
                                        value={values.dob}
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                    />
                                </div>
                                <div className="mb-4">
                                    <Form.Label>Email:</Form.Label>
                                    <Form.Control
                                        type="text"
                                        name="email"
                                        placeholder="Nhp email..."
                                        value={values.email}
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                    />
                                </div>
                                <div className="mb-4">
                                    <Form.Label>S in thoi:</Form.Label>
                                    <Form.Control
                                        type="text"
                                        name="phone"
                                        placeholder="Nhp s in thoi..."
                                        value={values.phone}
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                    />
                                </div>
                                <div className="mb-4">
                                    <Form.Label>Phng ban:</Form.Label>
                                    <Form.Control
                                        type="text"
                                        name="dep"
                                        placeholder="Nhp tn phng ban..."
                                        value={values.dep}
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                    />
                                </div>
                                <div className="mb-4">
                                    <Form.Label>Chc v:</Form.Label>
                                    <Form.Control
                                        type="text"
                                        name="pos"
                                        placeholder="Nhp tn chc v..."
                                        value={values.pos}
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                    />
                                </div>
                                <Button
                                    type="submit"
                                    className="d-table m-auto"
                                >
                                    p dng
                                </Button>
                            </Form>
                        )
                    }
                </Formik>
            </Offcanvas.Body>
        </Offcanvas>
    )
}

export default FiltersAdvanced