import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import { Button, Dropdown, Form, Modal } from 'react-bootstrap'
import { BsShieldLock } from 'react-icons/bs'

import { updateEmployee } from '~/redux/employeesSlice'


const ResetPassword = ({ employee }) => {
    const dispatch = useDispatch()
    const [visibleResetPassword, setVisibleResetPassword] = useState(false)              // State hin th thng bo xc nhn xa nhn vin
    const [info, setInfo] = useState({
        password: "",
        repassword: ""
    })

    const handleInputChange = (e) => {
        setInfo({
            ...info,
            [e.target.name]: e.target.value
        })
    }

    /* X l Submit Form */
    const [validated, setValidated] = useState(false)
    const handleResetPassword = (e) => {
        const form = e.currentTarget
        if (form.checkValidity() === false) {
            e.preventDefault()
            e.stopPropagation()
        }
        setValidated(true)
        if (form.checkValidity() === true) {
            e.preventDefault()
            e.stopPropagation()
            dispatch(updateEmployee({
                ...employee,
                user: {
                    ...info.user,
                    password: info.password
                }
            }))
            setVisibleResetPassword(false)
        }
    }
    //

    return (
        <>
            <Dropdown.Item
                component="button"
                onClick={() => setVisibleResetPassword(!visibleResetPassword)}
            >
                <BsShieldLock /> Cp li mt khu
            </Dropdown.Item>
            <Modal
                scrollable
                centered
                size="lg"
                show={visibleResetPassword}
                onHide={() => setVisibleResetPassword(false)}
            >
                <Modal.Header closeButton>
                    <Modal.Title>
                        Cp li mt khu - {employee.name}
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form
                        className="modal-body"
                        noValidate
                        validated={validated}
                        onSubmit={handleResetPassword}
                    >
                        <div className="mb-3">
                            <Form.Label htmlFor="password">Mt khu:</Form.Label>
                            <Form.Control
                                type="password"
                                name="password"
                                placeholder="Nhp mt khu..."
                                value={info.password}
                                onChange={handleInputChange}
                            />
                            <Form.Control.Feedback type="invalid">
                                Vui lng nhp mt khu.
                            </Form.Control.Feedback>
                        </div>
                        <div className="mb-3">
                            <Form.Label htmlFor="repassword">Nhp li mt khu:</Form.Label>
                            <Form.Control
                                type="password"
                                name="repassword"
                                placeholder="Nhp li mt khu..."
                                value={info.repassword}
                                onChange={handleInputChange}
                                isInvalid={!(info.password === info.repassword)}
                            />
                            <Form.Control.Feedback type="invalid">
                                Mt khu nhp li khng trng khp
                            </Form.Control.Feedback>
                        </div>
                        <Button variant="primary">
                            Xc nhn
                        </Button>
                    </Form>
                </Modal.Body>
            </Modal>
        </>
    )
}

export default ResetPassword